/* loginpage.css */

#login-page-main {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  background: url('https://images.unsplash.com/photo-1508780709619-79562169bc64?auto=format&fit=crop&w=1950&q=80') no-repeat center center fixed;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
}

#login-main {
  background-color: rgba(255, 255, 255, 0.95);
  padding: 40px 30px;
  border-radius: 20px;
  width: 100%;
  max-width: 420px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(10px);
  animation: slideFade 0.5s ease-in-out;
}

@keyframes slideFade {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

#Login-form h3 {
  text-align: center;
  margin-bottom: 25px;
  font-size: 26px;
  color: #333;
  font-weight: bold;
}

#Login-form  input {
  width: 100%;
  padding: 14px 16px;
  margin-bottom: 20px;
  border: 1px solid #ccc;
  border-radius: 12px;
  font-size: 15px;
  background-color: #fff;
  outline: none;
  transition: 0.3s;
}

#Login-form input:focus {
  box-shadow: 0 0 6px rgba(0, 123, 255, 0.3);
  background-color: #007bff18;
}

#Login-form button {
  width: 100%;
  padding: 13px;
  background-color: #007bffbd;
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.3s;
  margin-top: 10px;
}
#Login-form button:first-of-type{
  background-color: #333;
}

#Login-form button:hover {
  background-color: #007bfff6;
}

@media (max-width: 480px) {
  #login-main {
    padding: 25px 20px;
    border-radius: 14px;
  }

  #Login-form input, button {
    font-size: 14px;
    padding: 12px;
  }

  #Login-form h3 {
    font-size: 22px;
  }
}

