/* 🌌 पृष्ठभूमि और लेआउट */
#admin-s-edit {
    background-image: url('https://images.unsplash.com/photo-1508780709619-79562169bc64?auto=format&fit=crop&w=1950&q=80');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
  }
  
  /* 📝 फॉर्म कंटेनर */
  #admin-s-container {
    background-color: rgba(255, 255, 255, 0.95);
    padding: 30px 40px;
    border-radius: 12px;
    box-shadow: 0 8px 24px rgba(0, 0, 0, 0.2);
    max-width: 500px;
    width: 100%;
    box-sizing: border-box;
  }
  
  /* 🧾 हेडिंग */
  #admin-s-container h2 {
    text-align: center;
    margin-bottom: 25px;
    color: #333;
    font-size: 24px;
  }
  
  /* 🏷️ लेबल्स */
  #admin-s-container label {
    display: block;
    margin-bottom: 8px;
    font-weight: 600;
    color: #555;
    font-size: 17px;
  }
  
  /* 🖊️ इनपुट फ़ील्ड्स */
  #admin-s-container input {
    width: 100%;
    padding: 12px 14px;
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 6px;
    font-size: 18px;
    box-sizing: border-box;
    transition: border-color 0.3s;
  }
  
  #admin-s-container input:focus {
    border-color: #007bff;
    outline: none;
  }
  
  /* ✅ सबमिट बटन */
  #admin-s-container button {
    width: 100%;
    padding: 14px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 6px;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  #admin-s-container button:hover {
    background-color: #0056b3;
  }
  
  /* 📱 उत्तरदायी डिज़ाइन */
  @media (max-width: 600px) {
    #admin-s-container {
      padding: 20px;
    }
  
    #admin-s-container h2 {
      font-size: 20px;
    }
  
    #admin-s-container input,
    #admin-s-container button {
      font-size: 14px;
    }
  }
  